	.header-navbar li a:hover,
	.header-navbar .menu-btn:hover {
	  background-color: #FFFFFF;
	}

	.header-navbar .logo {
	  padding-top: 10px;
	  display: block;
	  float: left;
	  text-decoration: none;
	  border-bottom: 0;
	}


	/* menu */

	.header-navbar .menu {
	  clear: both;
	  max-height: 0;
	  transition: max-height .2s ease-out;
	}

	/* menu icon */

	.header-navbar .menu-icon {
	  cursor: pointer;
	  display: inline-block;
	  
	  padding: 28px 20px;
	  position: relative;
	  user-select: none;
	}

	.header-navbar .menu-icon .navicon {
	  background: #333;
	  display: block;
	  height: 2px;
	  position: relative;
	  transition: background .2s ease-out;
	  width: 18px;
	}

	.header-navbar .menu-icon .navicon:before,
	.header-navbar .menu-icon .navicon:after {
	  background: #333;
	  content: '';
	  display: block;
	  height: 100%;
	  position: absolute;
	  transition: all .2s ease-out;
	  width: 100%;
	}

	.header-navbar .menu-icon .navicon:before {
	  top: 5px;
	}

	.header-navbar .menu-icon .navicon:after {
	  top: -5px;
	}

	/* menu btn */

	.header-navbar .menu-btn {
	  display: none;
	}

	.header-navbar .menu-btn:checked ~ .menu {
	  max-height: 420px;
	}

	.header-navbar .menu-btn:checked ~ .menu-icon .navicon {
	  background: transparent;
	}

	.header-navbar .menu-btn:checked ~ .menu-icon .navicon:before {
	  transform: rotate(-45deg);
	}

	.header-navbar .menu-btn:checked ~ .menu-icon .navicon:after {
	  transform: rotate(45deg);
	}

	.header-navbar .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before,
	.header-navbar .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after {
	  top: 0;
	}



	@media (min-width: 768px) {
	  .header-navbar li {
	    float: left;
	  }

	  .header-navbar li a {
	    padding: 20px 30px;
	  }
	  .header-navbar .menu {
	    clear: none;
	    float: right;
	    max-height: none;
	  }
	  .header-navbar .menu-icon {
	    display: none;
	  }
	}




